.Smoothie {
    margin: auto;
    width: 100%;
    height: 357px;
    overflow: auto;
    text-align: center;
    font-weight: bold;
    font-size: .8rem;
}

@media (min-width: 500px) and (max-height: 400px) {
    .Smoothie {
        height: 300px;
    }
}
@media (min-width: 500px) and (min-height: 401px) {
    .Smoothie {
        width: 450px;
        height: 357px;
    }
}
@media (min-width: 1000px) and (min-height: 700px) {
    .Smoothie {
        width: 700px;
        height: 460px;
    }
}

.Smoothie p {
    opacity: 1;
    transition: opacity .1s ease-out;
    transform: translateY(-50px);
}
